* {
    box-sizing: border-box;
}
body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background-color: #eff3f7;
    height: 100%;
}

html,
body,
#root,
.container {
    height: 100%;
    display: flex;
    justify-content: center;
}

.loader {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.movies {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 48px;
    padding-top: 72px;
    width: 80%;
}

.movies .movie {
    width: 45%;
    background-color: white;
    margin-bottom: 72px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    font-weight: 300;
    padding: 20px;
    position: relative;
}

.movie img {
    position: relative;
    top: -50px;
    max-width: 120px;
    width: 100%;
    margin-right: 30px;
}

.movie h3,
.movie .movie__year {
    font-weight: 300;
    margin: 0;
}

.movie h3 {
    margin-bottom: 4px;
    font-size: 24px;
}

.movie .genres {
    color: #b2b3bc;
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
}

.genres li {
    padding-left: 4px;
}